// Constants
const ENCODING = 'aes128gcm';
const PRK_INFO = 'WebPush: info\0';
const CEK_INFO = 'Content-Encoding: aes128gcm\0';
const NONCE_INFO = 'Content-Encoding: nonce\0';
const MAX_PAYLOAD_LENGTH = 3993;

// Remove unused constants
// const MAX_SUBSCRIPTION_AGE = 180 * 24 * 60 * 60 * 1000;

// Utils
function base64UrlEncode(input) {
  return btoa(input)
    .replace(/\+/g, '-')
    .replace(/\//g, '_')
    .replace(/=+$/, '');
}

function base64UrlToUint8Array(base64UrlData) {
  const base64 = base64UrlData.replace(/-/g, '+').replace(/_/g, '/');
  const rawData = atob(base64);
  const buffer = new Uint8Array(rawData.length);
  for (let i = 0; i < rawData.length; ++i) {
    buffer[i] = rawData.charCodeAt(i);
  }
  return buffer;
}

function concatUint8Arrays(...arrays) {
  const totalLength = arrays.reduce((acc, arr) => acc + arr.length, 0);
  const result = new Uint8Array(totalLength);
  let offset = 0;
  for (const arr of arrays) {
    result.set(arr, offset);
    offset += arr.length;
  }
  return result;
}

// JWT Implementation
async function sign(header, payload, privateKey) {
  const content = `${base64UrlEncode(JSON.stringify(header))}.${base64UrlEncode(JSON.stringify(payload))}`;

  // Convert raw private key to EC key
  const rawKey = base64UrlToUint8Array(privateKey);
  const keyData = {
    kty: 'EC',
    crv: 'P-256',
    x: base64UrlEncode(String.fromCharCode(...rawKey.slice(1, 33))),
    y: base64UrlEncode(String.fromCharCode(...rawKey.slice(33, 65))),
    d: base64UrlEncode(String.fromCharCode(...rawKey)),
    ext: true,
  };

  const key = await crypto.subtle.importKey(
    'jwk',
    keyData,
    {
      name: 'ECDSA',
      namedCurve: 'P-256'
    },
    false,
    ['sign']
  );

  const signature = await crypto.subtle.sign(
    {
      name: 'ECDSA',
      hash: { name: 'SHA-256' }
    },
    key,
    new TextEncoder().encode(content)
  );

  return `${content}.${base64UrlEncode(String.fromCharCode(...new Uint8Array(signature)))}`;
}

// Encryption Implementation
async function generateEncryptionKeys() {
  return crypto.subtle.generateKey(
    {
      name: 'ECDH',
      namedCurve: 'P-256'
    },
    true,
    ['deriveBits']
  );
}

async function deriveSharedSecret(privateKey, p256dh, auth) {
  const publicKey = await crypto.subtle.importKey(
    'raw',
    base64UrlToUint8Array(p256dh),
    {
      name: 'ECDH',
      namedCurve: 'P-256'
    },
    true,
    []
  );

  const bits = await crypto.subtle.deriveBits(
    {
      name: 'ECDH',
      public: publicKey
    },
    privateKey,
    256
  );

  const prk = await crypto.subtle.importKey(
    'raw',
    bits,
    { name: 'HKDF' },
    false,
    ['deriveBits']
  );

  const info = concatUint8Arrays(
    new TextEncoder().encode(PRK_INFO),
    base64UrlToUint8Array(p256dh),
    base64UrlToUint8Array(auth)
  );

  return crypto.subtle.deriveBits(
    {
      name: 'HKDF',
      hash: { name: 'SHA-256' },
      salt: base64UrlToUint8Array(auth),
      info
    },
    prk,
    256
  );
}

async function deriveEncryptionKeys(secret, salt) {
  const keyHkdf = await crypto.subtle.importKey(
    'raw',
    secret,
    {
      name: 'HKDF'
    },
    false,
    ['deriveBits']
  );

  const cekInfo = new TextEncoder().encode(CEK_INFO);
  const nonceInfo = new TextEncoder().encode(NONCE_INFO);

  const cekBits = await crypto.subtle.deriveBits(
    {
      name: 'HKDF',
      hash: { name: 'SHA-256' },
      salt,
      info: cekInfo
    },
    keyHkdf,
    256
  );

  const nonceBits = await crypto.subtle.deriveBits(
    {
      name: 'HKDF',
      hash: { name: 'SHA-256' },
      salt,
      info: nonceInfo
    },
    keyHkdf,
    96
  );

  const contentEncryptionKey = await crypto.subtle.importKey(
    'raw',
    cekBits,
    {
      name: 'AES-GCM',
      length: 256
    },
    false,
    ['encrypt']
  );

  return { contentEncryptionKey, nonce: new Uint8Array(nonceBits) };
}

// Main WebPush Implementation
async function createWebPushRequest(subscription, payload, options) {
  const { endpoint, keys: { p256dh, auth } } = subscription;
  const { vapidPublicKey, vapidPrivateKey, subject } = options;

  // Generate encryption keys
  const localKeyPair = await generateEncryptionKeys();
  const localPublicKey = new Uint8Array(await crypto.subtle.exportKey('raw', localKeyPair.publicKey));
  
  // Generate salt
  const salt = crypto.getRandomValues(new Uint8Array(16));

  // Derive shared secret and encryption keys
  const sharedSecret = await deriveSharedSecret(localKeyPair.privateKey, p256dh, auth);
  const { contentEncryptionKey, nonce } = await deriveEncryptionKeys(sharedSecret, salt);

  // Encrypt payload
  const cipherText = await crypto.subtle.encrypt(
    { name: 'AES-GCM', iv: nonce },
    contentEncryptionKey,
    typeof payload === 'string' ? new TextEncoder().encode(payload) : payload
  );

  // Create VAPID JWT
  const audience = new URL(endpoint).origin;
  const jwt = await sign(
    { typ: 'JWT', alg: 'ES256' },
    { aud: audience, exp: Math.floor(Date.now() / 1000) + 12 * 3600, sub: subject },
    vapidPrivateKey
  );

  // Create request
  return new Request(endpoint, {
    method: 'POST',
    headers: {
      'TTL': '180',
      'Content-Type': 'application/octet-stream',
      'Content-Encoding': ENCODING,
      'Authorization': `vapid t=${jwt}, k=${vapidPublicKey}`
    },
    body: concatUint8Arrays(
      salt,
      new Uint8Array([localPublicKey.length]),
      localPublicKey,
      new Uint8Array(cipherText)
    )
  });
}

// Worker Implementation
export default {
  async fetch(request, env) {
    try {
      const url = new URL(request.url);
      
      // CORS headers
      const corsHeaders = {
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Methods': 'GET, POST, DELETE, OPTIONS',
        'Access-Control-Allow-Headers': 'Content-Type, Authorization',
      };

      // Handle CORS preflight
      if (request.method === 'OPTIONS') {
        return new Response(null, { 
          headers: {
            ...corsHeaders,
            'Access-Control-Max-Age': '86400',
          }
        });
      }

      // PWA Authentication Check
      const isPWA = url.searchParams.has('pwa');
      const authToken = url.searchParams.get('authToken');
      
      if (isPWA && !authToken) {
        // Redirect to authentication
        const currentUrl = encodeURIComponent(request.url);
        return Response.redirect(`https://barzo.work?redirect=${currentUrl}`);
      }

      if (authToken) {
        // Parse user info from authToken
        try {
          console.log('Auth token received:', {
            length: authToken.length,
            sample: authToken.substring(0, 20) + '...'
          });
          const userInfo = JSON.parse(decodeURIComponent(authToken));
          console.log('Parsed user info:', userInfo);
          
          // Create subscription key using identity
          const subscriptionKey = `subscription:${userInfo?.token?.identity.userId}`;
          
          // Try to get existing subscription
          let subscription = await env.SUBSCRIPTIONS.get(subscriptionKey);
          
          if (!subscription) {
            // Create new subscription with identity binding
            const newSubscription = {
              userId: userInfo.id,
              createdAt: new Date().toISOString(),
              lastUsed: new Date().toISOString()
            };
            
            await env.SUBSCRIPTIONS.put(subscriptionKey, JSON.stringify(newSubscription));
            subscription = JSON.stringify(newSubscription);
          }
        } catch (error) {
          console.error('Auth token parsing error:', {
            error: error.message,
            errorType: error.name,
            tokenLength: authToken?.length
          });
        }
      }

      // Route handling
      if (url.pathname === '/api/vapidPublicKey' && request.method === 'GET') {
        return new Response(
          JSON.stringify({ publicKey: env.VAPID_PUBLIC_KEY }), 
          { headers: { 'Content-Type': 'application/json', ...corsHeaders } }
        );
      }

      if (url.pathname === '/api/subscribe' && request.method === 'POST') {
        const subscription = await request.json();
        if (!subscription || !subscription.endpoint) {
          return new Response(JSON.stringify({ error: 'Invalid subscription' }), {
            status: 400,
            headers: { 'Content-Type': 'application/json', ...corsHeaders }
          });
        }

        // Get auth token and subscription key
        const authToken = request.headers.get('Authorization')?.split('Bearer ')?.[1];
        let subscriptionKey = subscription.endpoint;
        let userId = 'anonymous';

        if (authToken) {
          try {
            const userInfo = JSON.parse(decodeURIComponent(authToken));
            userId = userInfo?.token?.identity.userId;
            subscriptionKey = `subscription:${userId}`;
          } catch (error) {
            console.error('Invalid auth token:', error);
          }
        }

        // Store subscription in KV
        await env.SUBSCRIPTIONS.put(
          subscriptionKey,
          JSON.stringify({
            subscription,
            userId,
            createdAt: new Date().toISOString(),
            lastUsed: new Date().toISOString()
          })
        );

        return new Response(JSON.stringify({ success: true }), {
          headers: { 'Content-Type': 'application/json', ...corsHeaders }
        });
      }

      if (url.pathname === '/api/notify' && request.method === 'POST') {
        const { message, key } = await request.json();
        
        // If key is provided, only notify that specific subscription
        if (key) {
          const data = await env.SUBSCRIPTIONS.get(key, 'json');
          if (!data) {
            return new Response(JSON.stringify({ error: 'Subscription not found' }), {
              status: 404,
              headers: { 'Content-Type': 'application/json', ...corsHeaders }
            });
          }

          try {
            // Create and send push notification
            const pushRequest = await createWebPushRequest(
              data.subscription,
              JSON.stringify({
                title: 'Barzo',
                body: message || 'You have a new message!',
                icon: '/icon-192.png',
                badge: '/icon-192.png',
                data: { url: '/?source=push' }
              }),
              {
                vapidPublicKey: env.VAPID_PUBLIC_KEY,
                vapidPrivateKey: env.VAPID_PRIVATE_KEY,
                subject: 'mailto:admin@barzo.app'
              }
            );

            const response = await fetch(pushRequest);
            if (!response.ok) throw new Error(`Push service responded with ${response.status}`);

            // Update last used timestamp
            await env.SUBSCRIPTIONS.put(key, JSON.stringify({
              ...data,
              lastUsed: new Date().toISOString()
            }));

            return new Response(JSON.stringify({ success: true }), {
              headers: { 'Content-Type': 'application/json', ...corsHeaders }
            });
          } catch (error) {
            console.error('Push notification failed:', error);
            // Remove failed subscription
            await env.SUBSCRIPTIONS.delete(key);
            return new Response(JSON.stringify({ error: error.message }), {
              status: 500,
              headers: { 'Content-Type': 'application/json', ...corsHeaders }
            });
          }
        }

        // Broadcast to all subscriptions
        const subscriptionList = await env.SUBSCRIPTIONS.list();
        const results = [];

        for (const key of subscriptionList.keys) {
          const data = await env.SUBSCRIPTIONS.get(key.name, 'json');
          if (!data) continue;

          try {
            const pushRequest = await createWebPushRequest(
              data.subscription,
              JSON.stringify({
                title: 'Barzo',
                body: message || 'You have a new message!',
                icon: '/icon-192.png',
                badge: '/icon-192.png',
                data: { url: '/?source=push' }
              }),
              {
                vapidPublicKey: env.VAPID_PUBLIC_KEY,
                vapidPrivateKey: env.VAPID_PRIVATE_KEY,
                subject: 'mailto:admin@barzo.app'
              }
            );

            const response = await fetch(pushRequest);
            if (!response.ok) throw new Error(`Push service responded with ${response.status}`);

            // Update last used timestamp
            await env.SUBSCRIPTIONS.put(
              key.name,
              JSON.stringify({
                ...data,
                lastUsed: new Date().toISOString()
              })
            );

            results.push({ success: true, endpoint: key.name });
          } catch (error) {
            results.push({ success: false, endpoint: key.name, error: error.message });
            // Remove failed subscription
            await env.SUBSCRIPTIONS.delete(key.name);
          }
        }

        return new Response(JSON.stringify({ 
          results,
          activeSubscriptions: results.filter(r => r.success).length 
        }), {
          headers: { 'Content-Type': 'application/json', ...corsHeaders }
        });
      }

      if (url.pathname === '/api/subscription-status' && request.method === 'GET') {
        const authToken = request.headers.get('Authorization')?.split('Bearer ')?.[1];
        if (!authToken) {
          return new Response(JSON.stringify({ error: 'No auth token provided' }), {
            status: 401,
            headers: { 'Content-Type': 'application/json', ...corsHeaders }
          });
        }

        try {
          const userInfo = JSON.parse(decodeURIComponent(authToken));
          const userId = userInfo?.token?.identity.userId;
          const subscriptionKey = `subscription:${userId}`;
          
          // Get subscription data
          const data = await env.SUBSCRIPTIONS.get(subscriptionKey, 'json');
          
          return new Response(JSON.stringify({
            isSubscribed: !!data,
            subscriptionKey: subscriptionKey,
            subscription: data
          }), {
            headers: { 'Content-Type': 'application/json', ...corsHeaders }
          });
        } catch (error) {
          return new Response(JSON.stringify({ error: error.message }), {
            status: 500,
            headers: { 'Content-Type': 'application/json', ...corsHeaders }
          });
        }
      }

      if (url.pathname === '/sw.js') {
        const response = await fetch(request);
        const headers = new Headers(response.headers);
        headers.set('Content-Security-Policy', "script-src 'self' https://js.pusher.com");
        return new Response(response.body, {
          status: response.status,
          statusText: response.statusText,
          headers: headers
        });
      }

      return new Response('Not found', { status: 404, headers: corsHeaders });
    } catch (error) {
      return new Response(JSON.stringify({ error: error.message }), {
        status: 500,
        headers: { 'Content-Type': 'application/json', ...corsHeaders }
      });
    }
  }
}; 